/// 
/// 

.MountainHeader {
    overflow: hidden;
    position: relative;

    &__title {
        z-index: 2;
        color  : #54524f;
    }

    &__explore {
        z-index: -1;
        color  : #54524f;
    }
}


.title_mountains {
    overflow        : visible;
    background-color: rgba(153, 146, 141, 0.9);
    position        : relative;


    @include adapt-value('margin-top', 25, 5, 1);

    font-family: "Open Sans";

    &::before {
        content: '';

        top       : 0;
        left      : 0;
        width     : 100%;
        height    : 100%;
        background: url('../../../content/images/TitleMountains/Top_.png') 0% 0%/ 300% 19% no-repeat;
        position  : absolute;
        animation : wave 80s linear infinite alternate-reverse;

        @media (max-width:767px) {
            top: -30px;

        }

        @media (max-width:360px) {
            top: -33px;

        }

        @media (min-width:280px) and (max-width:320px) {
            top: -35px;

        }

    }

    &::after {
        content   : '';
        top       : 0;
        left      : 0;
        width     : 100%;
        height    : 110%;
        background: url('../../../content/images/TitleMountains/Bottom_.png') 0% 100%/ 150% 19% no-repeat;
        position  : absolute;
        z-index   : 1;
        animation : wave2 80s linear infinite alternate-reverse;

        @media (max-width:767px) {

            height: 115%;
        }

        @media (max-width:360px) {
            height: 115%;

        }
    }




    &__body {
        position: relative;
        z-index : 2;


        @include adapt-value('padding-top', 300, 40, 0);
        @include adapt-value('padding-bottom', 150, 65, 0);
        position: relative;

        @include adapt-value('min-height', 768, 300, 0);
        display       : flex;
        flex-direction: column;

        &::before {
            content   : '';
            position  : absolute;
            left      : 0;
            top       : 0;
            width     : 100%;
            height    : 100%;
            background: url('../../../content/images/TitleMountains/Left_.png') 0% 50%/ 45% 65% no-repeat;
            animation : increase 10s linear infinite alternate-reverse;

            @media (max-width:1200px) {
                background: url('../../../content/images/TitleMountains/Left_.png') 0% 50%/ 75vw 50vw no-repeat;

            }
        }
    }

    &__container {
        padding    : 0 15px;
        flex-grow  : 1;
        margin     : 0 auto;
        max-width  : 1050px;
        display    : flex;
        align-items: center;
    }

    &__content {}







}




.content {

    &__body {
        text-align : center;
        line-height: 1.2;

         div:last-child {
          
max-width: 770px

        }

    }

    &__title {
        @include adapt-value('margin-bottom', 60, 20, 0);

        &>p {


            @include adapt-value('font-size', 58.275, 24, 0);
            color: rgb(255, 255, 255);

        }

    }

    &__sub_title {

        &>p {

            hyphens        : auto;
            -moz-hyphens   : auto;
            -webkit-hyphens: auto;
            -ms-hyphens    : auto;

            display: block;
            padding: 0 5px;


            @include adapt-value('font-size', 29, 14.5, 0);

            color: rgb(219, 219, 219);

        }

        @include adapt-value(margin-bottom, 80, 21, 0);




    }
}